import request from 'assets/js/network';

async function getSingerList({
  limit = 10,
  type = 1,
  area = -1,
  initial,
}) {
  let result;
  try {
    result = await request({
      url: '/artist/list',
      params: {
        limit,
        type,
        area,
        initial,
      },
    });
    result = result.artists.map((el) => ({
      name: el.name,
      singerIcon: el.picUrl,
      singerId: el.id,
    }));
  } catch (err) {
    return Promise.reject(err);
  }
  return result;
}

async function getHotSingerList() {
  let result;
  try {
    result = await request({
      url: '/top/artists',
      params: {
        limit: 12,
      },
    });
    result = result.artists.map((el) => ({
      name: el.name,
      singerIcon: el.picUrl,
      singerId: el.id,
    }));
  } catch (err) {
    return Promise.reject(err);
  }
  return result;
}

export {
  getHotSingerList,
  getSingerList,
};
