<template>
  <div class="list-view">
    <scroll
      class="list-view-scroll"
      ref="scroll"
      :probeType="3"
      :isListenScroll="true"
      @scroll="scrollHandle"
    >
      <ul class="list-view-content" ref="list" @click="emitListClick($event)">
        <li
          class="list-view-group"
          v-for="(item, index) in listData"
          :key="index"
        >
          <h2 class="list-view-group-title">{{item[0]}}</h2>
          <ul class="list-view-group-content">
            <li
              class="list-view-group-item"
              v-for="(groupItem, groupIndex) in item[1]"
              :key="groupIndex"
              :data-id="groupItem ? groupItem.singerId : 0"
            >
              <img
                v-lazy="groupItem ? groupItem.singerIcon : './default.png'"
                class="list-view-group-item-icon"
              >
              <p class="list-view-group-item-name">
                {{groupItem ? groupItem.name : '正在加载'}}
              </p>
            </li>
          </ul>
        </li>
      </ul>
    </scroll>
    <ul
      class="shortcutList"
      @touchstart.stop.prevent="touchstartHandle($event)"
      @touchmove.stop.prevent="touchmoveHandle($event)"
    >
      <li
        v-for="(item, index) in shortcutList"
        :key="index"
        :data-index="index"
        class="shortcutItem"
        :class="{current: Number(currentIndex) === index}"
      >{{item}}</li>
    </ul>
    <div class="fixedTitle" v-show="fixedTitleShowed" ref="fixedTitle">
      <h2>{{currentTitle}}</h2>
    </div>
  </div>
</template>
<script>
import Scroll from 'components/common/scroll/Scroll.vue';
import debounce from 'assets/js/tool/debounce';

export default {
  components: {
    Scroll,
  },
  props: {
    listData: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      currentIndex: 0,
      fixedTitleShowed: false,
      diff: 0,
    };
  },
  methods: {
    refresh() {
      this.$refs.scroll.refresh();
    },
    touchstartHandle(event) {
      const target = event.target.closest('.shortcutItem');
      const firstTouch = event.touches[0];
      const { children } = this.$refs.list;
      this.$refs.scroll.scrollToElement(children[target.dataset.index]);
      this.currentIndex = target.dataset.index;
      this.shortcutItemStart.startItemY = target.offsetTop;
      this.shortcutItemStart.startY = firstTouch.pageY;
      this.shortcutItemStart.gap = target.offsetHeight;
    },
    touchmoveHandle(event) {
      const firstTouch = event.touches[0];
      const { pageY } = firstTouch;
      let totalMoveDistance = pageY
      - this.shortcutItemStart.startY + this.shortcutItemStart.startItemY;
      if (totalMoveDistance < 0) {
        totalMoveDistance = 0;
      }
      this.currentIndex = parseInt(totalMoveDistance / 18, 10);
      const { children } = this.$refs.list;
      if (this.currentIndex >= children.length) {
        this.currentIndex = children.length - 1;
      }
      this.$refs.scroll.scrollToElement(children[this.currentIndex]);
    },
    scrollHandle(position) {
      if (this.listHeight.length === 0) {
        return;
      }
      const y = -position.y;
      // 判断当处于下拉动作时，隐藏fixedTitle元素
      if (y < 0) {
        this.fixedTitleShowed = false;
        return;
      }
      this.fixedTitleShowed = true;
      if (y > this.listHeight[this.currentIndex] && y <= this.listHeight[this.currentIndex + 1]) {
        let diff = y - this.listHeight[this.currentIndex + 1];
        diff = diff >= -30 ? diff + 30 : 0;
        if (diff !== this.diff) {
          this.$refs.fixedTitle.style.transform = `translateY(-${diff}px)`;
          this.diff = diff;
        }
      } else {
        this.listHeight.some((item, index) => {
          if (y >= item && y <= this.listHeight[index + 1]) {
            this.currentIndex = index;
            return true;
          }
          return false;
        });
      }
    },
    emitListClick(event) {
      this.$emit('list-click', event);
    },
    _calculateHeight: debounce(function calculateHeight() {
      const { children } = this.$refs.list;
      const newListHeight = [];
      Array.from(children).forEach((el) => {
        newListHeight.push(el.offsetTop);
      });
      newListHeight.push(newListHeight[newListHeight.length - 1]
      + children[children.length - 1].offsetHeight);
      this.listHeight = newListHeight;
    }, 100),
  },
  created() {
    this.shortcutList = this.listData.map((el) => el[0].slice(0, 1));
    this.shortcutItemStart = {};
    this.listHeight = [];
  },
  watch: {
    listData() {
      this._calculateHeight();
    },
  },
  computed: {
    currentTitle() {
      return this.listData[this.currentIndex][0];
    },
  },
  emits: ['list-click'],
};
</script>
