          display: flex;
          padding: 20px 0 0 30px;
          align-items: center;
          .list-view-group-item-icon {
            width: 50px;
            height: 50px;
            border-radius: 50%;
          }
          .list-view-group-item-name {
            font-size: $font-size-medium;
