          }
        }
      }
    }
    .shortcutList {
      position: absolute;
      right: 0px;
      z-index: 10;
      top: 50%;
      transform: translateY(-50%);
      width: 20px;
      border-radius: 10px;
      text-align: center;
      background-color: $color-background-d;
      font-family: Helvetica;
      > li {
        padding: 3px;
        font-size: $font-size-small;
        color: $color-text-l;
        &.current {
