<template>
  <div class="singer-list">
    <base-list-view
      :listData="singerDataArr"
      @list-click="listClickHandle"
      ref="list"
    />
  </div>
</template>
<script>
import BaseListView from 'components/common/listView/BaseListView.vue';
import { getHotSingerList, getSingerList } from 'api/singer/singer';

export default {
  data() {
    const singerData = new Map();
    const tempArray = new Array(10);
    singerData.set('热门', tempArray);
    for (let i = 0; i < 26; i += 1) {
      const key = String.fromCharCode(65 + i);
      singerData.set(key, tempArray);
    }
    return {
      singerData,
      singerDataArr: Array.from(singerData),
    };
  },
  components: {
    BaseListView,
  },
  created() {
    this._getHotSingerInfo();
    for (let i = 0; i < 26; i += 1) {
      const key = String.fromCharCode(65 + i);
      this._getSingerInfo({ initial: key });
    }
  },
  methods: {
    refresh() {
      this.$refs.list.refresh();
    },
    listClickHandle(event) {
      const target = event.target.closest('.list-view-group-item');
      if (!target) {
        return;
      }
      const { id } = target.dataset;
      const { src } = target.querySelector('.list-view-group-item-icon').dataset;
      const name = target.querySelector('.list-view-group-item-name').innerText;
      this.$router.push({
        name: 'singerDetail',
        params: {
          id,
        },
        query: {
          src,
          name,
        },
      });
    },
    _getHotSingerInfo() {
      getHotSingerList().then((res) => {
        this.singerData.set('热门', res);
        this.singerDataArr = Array.from(this.singerData);
      }).catch((err) => {
        console.log('热门歌手信息获取失败', err);
      });
    },
    _getSingerInfo(options) {
      getSingerList(options).then((res) => {
        const key = options.initial.toUpperCase();
        this.singerData.set(key, res);
        this.singerDataArr = Array.from(this.singerData);
      }).catch((err) => {
        console.log('歌手信息获取失败', err);
      });
    },
  },
};
</script>
<style lang="scss" scoped>
  .singer-list {
    height: 100%;
    width: 100%;
  }
</style>
